<script setup>
import { ref, defineModel, defineProps } from "vue";

// Import comps
import AppIcon from "@/components/utility/icons/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppInput from "@/components/utility/text-input/index.vue";

// Refs
const image = defineModel();
const trigger = ref(null);
const props = defineProps(["placeholder"]);

/**
 * Opens the WordPress Media Library selector.
 *
 * This function performs the following operations:
 * 1. Checks if the WordPress Media Library is available.
 * 2. Creates a new media frame for selecting images.
 * 3. Sets up an event listener for when an image is selected.
 * 4. When an image is selected, it updates the uixpress_settings.logo with the selected image URL.
 * 5. Opens the media frame for user interaction.
 *
 * @function openMediaSelector
 * @returns {void}
 * @throws Will log an error to the console if the WordPress Media Library is not available.
 */
const openMediaSelector = (optionKey) => {
  // Check if the wp object exists
  if (typeof wp === "undefined" || typeof wp.media !== "function") {
    console.error("WordPress Media Library is not available");
    return;
  }

  // Create a new media frame
  var mediaFrame = wp.media({
    title: __("Select or Upload Media", "uixpress"),
    button: {
      text: __("Use this media", "uixpress"),
    },
    multiple: false, // Set to true if you want to allow multiple selections
    library: {
      type: "image", // Limit to image files only
    },
  });

  // When an image is selected in the media frame...
  mediaFrame.on("select", () => {
    // Get media attachment details from the frame state
    const attachments = mediaFrame.state().get("selection").toJSON();
    if (!Array.isArray(attachments)) return;

    const { url } = attachments[0];
    image.value = url;
  });

  // Open the media frame
  mediaFrame.open();
};
</script>

<template>
  <div class="flex flex-col w-48">
    <div
      class="relative group border border-zinc-200 dark:border-zinc-700 cursor-pointer rounded-tl-xl rounded-tr-xl w-48 h-32 flex flex-col place-content-center bg-zinc-50 dark:bg-zinc-800 overflow-hidden border-b-0"
      @click="openMediaSelector()"
    >
      <img v-if="image" :src="image" class="rounded-xl w-48 h-32 object-contain" />
      <AppIcon v-else icon="image" class="text-4xl my-auto mx-auto text-zinc-500 group-hover:text-zinc-900 group-hover:dark:text-zinc-100 transition-colors" />
      <div class="absolute opacity-0 group-hover:opacity-100 pt-1 pr-1 top-0 right-0" v-if="image">
        <AppButton type="transparent" @click.stop.prevent="image = ''">
          <AppIcon icon="close" />
        </AppButton>
      </div>
    </div>
    <AppInput v-model="image" type="text" :placeholder="placeholder || __('url', 'uixpress')" class="rounded-tl-none rounded-tr-none" />
  </div>
</template>
