<script setup>
import { ref, watch, nextTick, computed, watchEffect, defineAsyncComponent } from "vue";
import { ShadowRoot } from "vue-shadow-dom";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Funcs
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { isObject } from "@/assets/js/functions/isObject.js";
import { notify } from "@/assets/js/functions/notify.js";
import { hexToRgb } from "@/assets/js/functions/hexToRgb.js";
import { isNonProduction } from "@/assets/js/functions/isNonProduction.js";
import { slugify } from "@/assets/js/functions/slugify.js";
import { useDarkMode } from "./src/useDarkMode.js";
import { generateColorScheme } from "./src/generateColorScheme.js";
const { isDark } = useDarkMode();

// Comps
import Notifications from "@/components/utility/notifications/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";
import AppInput from "@/components/utility/text-input/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import StatusTag from "@/components/utility/status-tag/index.vue";
import AppToggle from "@/components/utility/toggle/index.vue";
import UserRoleSelect from "@/components/utility/multiselect-roles-and-users/index.vue";
import PostTypeSelect from "@/components/utility/post-type-select/index.vue";
import LoadingIndicator from "@/components/utility/loading-indicator/index.ts";
import ColorPicker from "@/components/utility/color-select/index.vue";
import ImageSelect from "@/components/utility/image-select/index.vue";
import AppTabs from "@/components/utility/tabs/index.vue";
const CodeEditor = defineAsyncComponent(() => import("@/components/utility/code-editor/index.vue"));
import TextPairs from "./src/text-pairs.vue";

// Refs
const adoptedStyleSheets = ref(new CSSStyleSheet());
const generatedColors = ref({});
const loading = ref(false);
const store = ref();
const uixpress_settings = ref({});
const newKey = ref("");
const originalSettings = ref({});
const keyactions = ref(false);
const saving = ref(false);
const settingsFetched = ref(false);
const tab = ref("general");
const settingsUploader = ref(null);
const settingsGroups = {
  general: { value: "general", label: __("General", "uixpress") },
  login: { value: "login", label: __("Login", "uixpress") },
  theme: { value: "theme", label: __("Theme", "uixpress") },
  whitelabel: { value: "whitelabel", label: __("White label", "uixpress") },
  menu: { value: "menu", label: __("Menu", "uixpress") },
  posts: { value: "posts", label: __("Posts", "uixpress") },
  plugins: { value: "plugins", label: __("Plugins", "uixpress") },
};
const submenuStyles = {
  click: { value: "click", label: __("Click", "uixpress") },
  hover: { value: "hover", label: __("Hover", "uixpress") },
};
const forceGlobalThemeOptions = {
  off: { value: "off", label: __("Off", "uixpress") },
  light: { value: "light", label: __("Light", "uixpress") },
  dark: { value: "dark", label: __("Dark", "uixpress") },
};

/**
 * Injects styles into shadow root
 */
const setStyles = () => {
  let appStyleNode = document.querySelector("#uixpress-settings-css");
  if (!appStyleNode) {
    appStyleNode = manuallyAddStyleSheet();
    // Wait for stylesheet to load
    appStyleNode.onload = () => {
      const appStyles = appStyleNode.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
  } else {
    const appStyles = appStyleNode.sheet;
    for (const rule of [...appStyles.cssRules].reverse()) {
      adoptedStyleSheets.value.insertRule(rule.cssText);
    }
  }

  //appStyleNode.remove();
};

const manuallyAddStyleSheet = () => {
  var link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = `${appStore.state.siteURL}/wp-content/plugins/uixpress/app/dist/assets/styles/settings.css`;
  document.head.appendChild(link);

  return link;
};

/**
 * Fetches site settings from the server and updates the application state.
 *
 * This function performs the following operations:
 * 1. Sets the application loading state to true.
 * 2. Fetches settings from the 'wp/v2/settings' endpoint.
 * 3. Updates the application loading state to false.
 * 4. If the response is valid and contains uixpress_settings, it updates the local state.
 * 5. Creates a deep copy of the settings for tracking original values.
 * 6. Sets a flag indicating that settings have been fetched.
 *
 * @async
 * @function getSettings
 * @returns {Promise<void>}
 * @throws Will not throw, but will return early if the response is invalid.
 */
const getSettings = async () => {
  appStore.updateState("loading", true);
  loading.value = true;

  const args = { endpoint: "wp/v2/settings", params: {} };
  const response = await lmnFetch(args);

  appStore.updateState("loading", false);
  loading.value = false;

  // Something went wrong
  if (!response) return;

  if (!response.data.uixpress_settings || !isObject(response.data.uixpress_settings)) return;

  uixpress_settings.value = response.data.uixpress_settings;

  if (uixpress_settings.value.custom_css) uixpress_settings.value.custom_css = decodeHTMLEntities(uixpress_settings.value.custom_css);

  originalSettings.value = JSON.parse(JSON.stringify(response.data.uixpress_settings));

  if (uixpress_settings.value.license_key) {
    if (!isValidLicenseKey(uixpress_settings.value.license_key)) {
      removeKey();
    }
  }

  nextTick(() => {
    settingsFetched.value = true;
    //validateLicence();
  });
};

// Helper function to decode HTML entities
const decodeHTMLEntities = (text) => {
  const textArea = document.createElement("textarea");
  textArea.innerHTML = text;
  return textArea.value;
};

/**
 * Updates the site settings on the server and in the local application state.
 *
 * This function performs the following operations:
 * 1. Sets the application loading state and local saving state to true.
 * 2. Creates a payload with the current uixpress_settings.
 * 3. Sends a POST request to the 'wp/v2/settings' endpoint with the payload.
 * 4. Updates the application loading state and local saving state to false.
 * 5. If the update is successful, it notifies the user and updates the application state.
 *
 * @async
 * @function updateSettings
 * @returns {Promise<void>}
 * @throws Will not throw, but will return early if the response is invalid.
 */
const updateSettings = async () => {
  appStore.updateState("loading", true);
  saving.value = true;

  let payload = {
    uixpress_settings: JSON.parse(JSON.stringify(uixpress_settings.value)),
  };

  const args = { endpoint: "wp/v2/settings", type: "POST", params: {}, data: payload };
  const response = await lmnFetch(args);

  appStore.updateState("loading", false);
  saving.value = false;

  // Something went wrong
  if (!response) return;

  notify({ title: __("Settings updated!", "uixpress"), type: "success" });

  appStore.updateState("uixpress_settings", uixpress_settings.value);
};

const exportSettings = () => {
  let payload = {
    uixpress_settings: JSON.parse(JSON.stringify(uixpress_settings.value)),
  };

  delete payload.uixpress_settings.license_key;
  delete payload.uixpress_settings.instance_id;

  // Convert the object to a JSON string with pretty formatting
  const jsonString = JSON.stringify(payload, null, 2);

  // Create a blob with the JSON data
  const blob = new Blob([jsonString], { type: "application/json" });

  // Create a URL for the blob
  const url = URL.createObjectURL(blob);

  // Create a temporary anchor element
  const link = document.createElement("a");
  link.href = url;
  link.download = `uixpress-settings.json`;

  // Append the link to the body, click it, and remove it
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);

  // Clean up by revoking the blob URL
  URL.revokeObjectURL(url);
};

// Example usage with file input:
const handleSettingsFileUpload = async (event) => {
  const file = event.target.files[0];
  try {
    await importFromSettingJSON(file);
    notify({ type: "success", title: __("Settings imported", "uixpress") });
  } catch (error) {}
};

const importFromSettingJSON = async (file) => {
  try {
    // Check if file is provided
    if (!file || !(file instanceof File)) {
      notify({ type: "error", title: __("Please provide a valid JSON file", "uixpress") });
      return;
    }

    // Read the file content
    const fileContent = await file.text();
    const jsonData = JSON.parse(fileContent);

    // Validate the required fields exist
    if (!jsonData.uixpress_settings) {
      notify({ type: "error", title: __("Invalid settings: must be an object", "uixpress") });
      return;
    }

    // Validate menu_meta is an object
    if (typeof jsonData.uixpress_settings !== "object" || jsonData.uixpress_settings === null) {
      notify({ type: "error", title: __("Invalid settings: must be an object", "uixpress") });
      return;
    }

    // Update the menu structure
    uixpress_settings.value = { ...uixpress_settings.value, ...jsonData.uixpress_settings };

    return true;
  } catch (error) {
    notify({ type: "error", title: __("Error uploading settings", "uixpress") });
    throw error;
  }
};

/**
 * Activates a license key for the application.
 *
 * This function performs the following operations:
 * 1. Sets the application loading state and keyactions state to true.
 * 2. Prepares an update object with the new license key and instance name.
 * 3. Sends a POST request to the 'uixpress/v1/lemonsqueezy' endpoint to activate the license.
 * 4. Validates the response, checking for the correct store ID.
 * 5. If activation is successful, updates the local settings with the new key and instance ID.
 * 6. Calls updateSettings to save the new license information.
 *
 * @async
 * @function activateLicence
 * @returns {Promise<void>}
 * @throws Will not throw, but will notify the user of errors and return early if the response is invalid.
 */
const activateLicence = async () => {
  appStore.updateState("loading", true);
  keyactions.value = true;

  const update = {
    license_key: newKey.value,
    instance_name: window.location.hostname,
  };

  const args = { endpoint: "uixpress/v1/lemonsqueezy", type: "POST", data: update, params: { ls_endpoint: `licenses/activate`, method: "POST", rawResponse: true, rawData: true } };
  const response = await lmnFetch(args);

  appStore.updateState("loading", false);
  keyactions.value = false;

  // Something went wrong
  if (!response) return;

  const store_id = response?.data?.meta?.store_id;
  const product_id = response?.data?.meta?.product_id;
  const variant_id = response?.data?.meta?.variant_id;

  if (store_id != "3120") {
    return notify({ title: __("Incorrect key", "uixpress"), type: "error" });
  }

  if (response.data.activated) {
    notify({ title: __("Key succesfully activated", "uixpress"), type: "success" });
    const instance = response?.data?.instance?.id;
    uixpress_settings.value.license_key = newKey.value;
    uixpress_settings.value.instance_id = instance;

    updateSettings();
  }
};

const validateLicence = async () => {
  // Nothing to validate
  if (!uixpress_settings.value.license_key || !uixpress_settings.value.instance_id) {
    return;
  }

  // Get properties
  const restNonce = appStore.state.restNonce;
  const restBase = appStore.state.restBase;

  const update = {
    license_key: uixpress_settings.value.license_key,
    instance_id: uixpress_settings.value.instance_id,
  };

  const payload = { method: type, headers: {} };
  payload.body = JSON.stringify(update);
  payload.headers = { "Content-Type": "application/json", "X-WP-Nonce": restNonce };

  // Build endpoint url
  const rest_url = new URL(`${restBase}uixpress/v1/lemonsqueezy`);
  rest_url.searchParams.append("ls_endpoint", "licenses/validate");
  rest_url.searchParams.append("method", "POST");
  rest_url.searchParams.append("rawResponse", true);
  rest_url.searchParams.append("rawData", true);

  // Do fetch
  const response = await fetch(rest_url.toString(), payload);

  // Generic error so bail
  if (!response.ok) return;

  // Get response
  const result = await response.json();

  if (response?.data?.valid && !response?.data?.error) {
    // all good
  }
};

/**
 * Removes (deactivates) the current license key.
 *
 * This function performs the following operations:
 * 1. Sets the application loading state and keyactions state to true.
 * 2. Prepares an update object with the current license key and instance ID.
 * 3. Sends a POST request to the 'uixpress/v1/lemonsqueezy' endpoint to deactivate the license.
 * 4. Clears the license key and instance ID from the local settings.
 * 5. Calls updateSettings to save the changes.
 * 6. Notifies the user of successful removal.
 *
 * @async
 * @function removeKey
 * @returns {Promise<void>}
 * @throws Will not throw, but will return early if the response is invalid.
 */
const removeKey = async () => {
  appStore.updateState("loading", true);
  keyactions.value = true;

  const update = {
    license_key: uixpress_settings.value.license_key,
    instance_id: uixpress_settings.value.instance_id,
  };

  const args = { endpoint: "uixpress/v1/lemonsqueezy", type: "POST", data: update, params: { ls_endpoint: `licenses/deactivate`, method: "POST", rawResponse: true, rawData: true } };
  const response = await lmnFetch(args);

  appStore.updateState("loading", false);
  keyactions.value = false;

  uixpress_settings.value.license_key = "";
  uixpress_settings.value.instance_id = "";
  newKey.value = "";

  nextTick(() => {
    updateSettings();
  });

  // Something went wrong
  if (!response) return;

  notify({ title: __("Instance removed", "uixpress"), type: "success" });
};

const isActivated = computed(() => {
  if (isNonProduction()) return true;

  if (uixpress_settings.value.license_key && uixpress_settings.value.instance_id) {
    return true;
  }

  return false;
});

/**
 * Validates a license key against a specific pattern.
 * The pattern is: XXXXXXXX-XXXX-XXXX-XXXX-XXXXXXXXXXXX
 * Where X is any uppercase letter or number.
 *
 * @param {string} licenseKey - The license key to validate.
 * @returns {boolean} True if the license key matches the pattern, false otherwise.
 */
function isValidLicenseKey(licenseKey) {
  // Regular expression pattern
  const pattern = /^[A-Z0-9]{8}-[A-Z0-9]{4}-[A-Z0-9]{4}-[A-Z0-9]{4}-[A-Z0-9]{12}$/;

  // Test the license key against the pattern
  return pattern.test(licenseKey);
}

const updateCssProperties = (cssvalues, colorName) => {
  for (let color of cssvalues) {
    const hexArray = hexToRgb(color.color);
    const variableName = `--uix-${colorName}-${color.step}`;
    document.documentElement.style.setProperty(variableName, hexArray.join(" "));
  }
};

const pushNewStylesheet = () => {
  if (!Array.isArray(uixpress_settings.value.external_stylesheets)) {
    uixpress_settings.value.external_stylesheets = [];
  }

  uixpress_settings.value.external_stylesheets.push("");
};

setStyles();
getSettings();

/* Base color watchers */
watch(
  () => uixpress_settings.value.base_theme_color,
  () => {
    if (!settingsFetched.value || !uixpress_settings.value.base_theme_color) return;
    uixpress_settings.value.base_theme_scale = generateColorScheme(uixpress_settings.value.base_theme_color);
    updateCssProperties(uixpress_settings.value.base_theme_scale, "base");
  }
);
watch(
  () => uixpress_settings.value.base_theme_scale,
  () => {
    if (!settingsFetched.value) return;
    updateCssProperties(uixpress_settings.value.base_theme_scale, "base");
  },
  { deep: true }
);

watch(
  () => uixpress_settings.value,
  () => {
    if (!settingsFetched.value) return;
    const settingsUpdateEvent = new CustomEvent("uipx-settings-update", { detail: uixpress_settings.value });
    document.dispatchEvent(settingsUpdateEvent);
  },
  { deep: true }
);

/* Accent color watchers */
watch(
  () => uixpress_settings.value.accent_theme_color,
  () => {
    if (!settingsFetched.value || !uixpress_settings.value.accent_theme_color) return;
    uixpress_settings.value.accent_theme_scale = generateColorScheme(uixpress_settings.value.accent_theme_color);
    updateCssProperties(uixpress_settings.value.accent_theme_scale, "accent");
  }
);
watch(
  () => uixpress_settings.value.accent_theme_scale,
  () => {
    if (!settingsFetched.value) return;
    updateCssProperties(uixpress_settings.value.accent_theme_scale, "accent");
  },
  { deep: true }
);

watchEffect(() => {
  // No path set
  if (!uixpress_settings.value?.login_path) return;

  let updated = slugify(uixpress_settings.value.login_path);

  if (updated.startsWith("/")) updated.substring(1);

  uixpress_settings.value.login_path = updated;
});
</script>

<template>
  <!--Menu -->
  <component is="style"> html{font-size:14px;}#wpcontent{padding:0}</component>
  <ShadowRoot tag="div" :adopted-style-sheets="[adoptedStyleSheets]" class="uipx-normalize">
    <component is="style"> :root{font-size:14px}#wpcontent{padding:0}</component>
    <div
      class="bg-white dark:bg-zinc-900 uipx-normalize font-sans dark:bg-zinc-900"
      :class="[isDark ? 'dark' : '', uixpress_settings?.layout == 'bubble' ? 'bg-zinc-100/40' : 'bg-white']"
      style="min-height: calc(100dvh - var(--wp-admin--admin-bar--height))"
    >
      <Notifications />
      <div class="translate-x-0">
        <LoadingIndicator :height="3" />
      </div>
      <div class="grid grid-cols-3 gap-12 p-6" v-if="!loading">
        <div class="flex flex-row place-content-between col-span-3 items-center">
          <div class="text-3xl font-semibold text-zinc-900 dark:text-zinc-100">{{ __("Settings", "uixpress") }}</div>
          <div class="flex flex-row items-center gap-2">
            <AppButton type="default" @click="exportSettings" class="text-sm">{{ __("Export", "uixpress") }}</AppButton>
            <AppButton type="default" @click="settingsUploader.click()" class="text-sm">
              {{ __("Import", "uixpress") }}
              <input type="file" accept=".json" @change="handleSettingsFileUpload" class="hidden" ref="settingsUploader" />
            </AppButton>
            <AppButton type="primary" @click="updateSettings" class="text-sm" :loading="saving">{{ __("Update", "uixpress") }}</AppButton>
          </div>
        </div>

        <AppTabs v-model="tab" :options="settingsGroups" class="col-span-3" />

        <template v-if="tab == 'general'">
          <!-- Store licence key-->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Licence key", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{ __("Activating uiXpress enables updates and features", "uixpress") }}</span>
          </div>

          <div class="col-span-2" v-if="uixpress_settings.license_key || uixpress_settings.instance_id">
            <div class="flex flex-row gap-3">
              <StatusTag status="success" :text="__('Licence Active', 'uixpress')" class="text-lg" />
              <AppButton type="warning" @click="removeKey" :loading="keyactions">{{ __("Remove key", "uixpress") }}</AppButton>
            </div>
          </div>

          <div v-else class="col-span-2">
            <div class="grid grid-cols-3 gap-3">
              <AppInput v-model="newKey" class="col-span-2" icon="key" :placeholder="__('uiXpress licence key', 'uixpress')" />
              <AppButton type="default" :disabled="!newKey" @click="activateLicence" :loading="keyactions">{{ __("Activate", "uixpress") }}</AppButton>
            </div>
            <div class="bg-emerald-100 dark:bg-emerald-600/20 border border-emerald-400 dark:border-emerald-700 p-2 rounded-lg text-sm mt-3" v-if="isNonProduction()">
              {{ __("Development server detected, no need to activate your licence key.", "uixpress") }}
            </div>
          </div>

          <div class="border-t border-zinc-200 dark:border-zinc-700 col-span-3"></div>
        </template>

        <div class="bg-emerald-100 dark:bg-emerald-600/20 border border-emerald-400 dark:border-emerald-700 p-2 rounded-lg text-sm mt-3 col-span-3" v-if="!isActivated">
          {{ __("Please add your licence key to unlock the plugin settings", "uixpress") }}
        </div>

        <template v-if="tab == 'general' && isActivated">
          <!-- Logo -->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Logo", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{ __("Optional logo for to be displayed in the menu", "uixpress") }}</span>
          </div>
          <div class="col-span-2 flex flex flex-col place-content-start gap-3">
            <ImageSelect v-model="uixpress_settings.logo" :placeholder="__('Logo url', 'uixpress')" />
          </div>

          <div class="border-t border-zinc-200 dark:border-zinc-700 col-span-3"></div>

          <!-- Dark Logo -->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Dark mode logo", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{
              __("Set an alternative logo to use in dark mode. If one isn't set it will fall back to the standard logo.", "uixpress")
            }}</span>
          </div>
          <div class="col-span-2 flex flex flex-col place-content-start gap-3">
            <ImageSelect v-model="uixpress_settings.dark_logo" :placeholder="__('Dark Logo url', 'uixpress')" />
          </div>

          <div class="border-t border-zinc-200 dark:border-zinc-700 col-span-3"></div>

          <!-- Role disable -->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Disable theme", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{ __("Choose roles or usersnames to disable the theme for.", "uixpress") }}</span>
          </div>
          <div class="col-span-2 flex flex-col">
            <UserRoleSelect v-model="uixpress_settings.disable_theme" class="max-w-[300px]" />
          </div>

          <div class="border-t border-zinc-200 dark:border-zinc-700 col-span-3"></div>

          <!-- Search post types -->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Search post types", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{ __("Choose which post types are available in the global search", "uixpress") }}</span>
          </div>
          <div class="col-span-2 flex flex-col">
            <PostTypeSelect v-model="uixpress_settings.search_post_types" class="max-w-[300px]" />
          </div>

          <div class="border-t border-zinc-200 dark:border-zinc-700 col-span-3"></div>

          <!-- Disable search-->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Disable global search", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{ __("Disable global search for all users.", "uixpress") }}</span>
          </div>
          <div class="col-span-2 flex flex-col">
            <AppToggle v-model="uixpress_settings.disable_search" class="max-w-[300px]" />
          </div>

          <div class="border-t border-zinc-200 dark:border-zinc-700 col-span-3"></div>

          <!-- Layout -->
          <div class="flex flex-col pt-2 gap-2">
            <span class="text-zinc-900 dark:text-zinc-100 font-semibold">{{ __("Admin layout", "uixpress") }}</span>
            <span class="text-zinc-500 dark:text-zinc-400 leading-snug">{{ __("Choose your prefered admin layout", "uixpress") }}</span>
          </div>
          <div class="col-span-2 flex flex-row items-start gap-3 flex-wrap">
            <!-- Default -->
            <div
              class="rounded flex flex-row h-32 aspect-video border border-zinc-200 dark:border-zinc-700 cursor-pointer transition-all"
              :class="!uixpress_settings.layout || uixpress_settings.layout == 'default' ? 'outline-2 outline-indigo-600 outline outline-offset-2' : ''"
              @click="uixpress_settings.layout = 'default'"
              :title="__('Default', 'uixpress')"
